import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Rx';
import { Api } from '../../../providers/api/api';

import { About } from './about.model';

@Injectable()
export class AboutService {
    private resourceUrl = Api.API_URL + '/abouts';

    constructor(private http: HttpClient) { }

    create(about: About): Observable<About> {
        return this.http.post(this.resourceUrl, about);
    }

    update(about: About): Observable<About> {
        return this.http.put(this.resourceUrl, about);
    }

    find(id: number): Observable<About> {
        return this.http.get(`${this.resourceUrl}/${id}`);
    }

    query(req?: any): Observable<any> {
        return this.http.get(this.resourceUrl);
    }

    delete(id: number): Observable<any> {
        return this.http.delete(`${this.resourceUrl}/${id}`, { observe: 'response', responseType: 'text' });
    }
}



